body {
  margin: 0px;
  padding: 0px;
  width: 100%;
  min-height: 100vh;
  background: #09090C;
  font-family: 'Roboto', sans-serif;
  color: #fff;
  overflow: hidden;
}

@keyframes dots {
  0%,
  20% {
    color: rgba(0, 0, 0, 0);
    text-shadow: 0.25em 0 0 rgba(0, 0, 0, 0), 0.5em 0 0 rgba(0, 0, 0, 0);
  }
  40% {
    color: white;
    text-shadow: 0.25em 0 0 rgba(0, 0, 0, 0), 0.5em 0 0 rgba(0, 0, 0, 0);
  }
  60% {
    text-shadow: 0.25em 0 0 white, 0.5em 0 0 rgba(0, 0, 0, 0);
  }
  80%,
  100% {
    text-shadow: 0.25em 0 0 white, 0.5em 0 0 white;
  }
}

.show {
  display: block;
}

.hidden {
  display: none;
}

.wrapper {
  height: 100vh;
  overflow-y: auto;
}

.loading-wrapper,
.not-found {
  text-align: center;
  font-size: 30px;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100vh;
  color: #fff;
}

.loading-wrapper .loading-msg:after,
.loading-users:after {
  content: ' .';
  animation: dots 1s steps(5, end) infinite;
}

.users-wrapper,
.settings-wrapper {
  margin: 50px 0px;
}

.users-wrapper ul {
  margin: 0px;
  padding: 0px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}

.user-info-card {
  width: 400px;
  height: 100px;
  background: #fff;
  border-radius: 10px;
  color: black;
  margin: 10px 35px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
}

.user-info-card img {
  width: 100px;
  border-radius: 10px 0px 0px 10px;
}

.user-info-details {
  float: right;
  background: #262630;
  color: #DDD7CF;
  height: 100%;
  width: 50%;
  padding: 0px 50px;
}

.user-info-details p,
.user-info-misc p {
  margin: 10px 0px;
}

.user-info-details .name {
  font-weight: bold;
  color: #fff;
}

.user-info-details .username,
.user-info-details .email,
.user-info-misc p {
  font-size: 13px;
}

.user-info-actions {
  float: right;
  margin-right: -25px;
  margin-top: -80px;
  cursor: pointer;
  font-size: 20px;
  border-radius: 50%;
  background: #FF6701;
  width: 30px;
  height: 30px;
  text-align: center;
  color: #fff;
}

.user-info-actions span {
  display: block;
  line-height: 28px;
}

.user-info-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
}

.user-info-modal-main {
  position: fixed;
  background: #F8D11C;
  width: 35%;
  height: auto;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.user-info-modal-main img {
  border-radius: 50%;
  margin: 30px 50px;
  width: auto;
  height: auto;
}

.user-info-modal-main .user-info-actions {
  position: absolute;
  right: 45px;
  top: 90px;
}

.user-info-modal-main span:not(.user-info-btn) {
  margin-right: 5px;
  font-size: 13px;
  font-weight: bold;
}

.user-info-modal-main p {
  font-weight: normal !important;
  font-size: 13px !important;
}

.search-wrapper {
  margin: 30px 0;
  height: 20px;
}

.search {
  margin: 0 auto;
  display: block;
  width: 30%;
  height: 30px;
  border: none;
  border-radius: 5px;
  padding: 10px;
  font-size: 14px;
}

.loading-users,
.end-users {
  text-align: center;
  font-weight: bold;
  font-size: 25px;
  margin-top: -45px;
  color: #fff;
}

.settings-link {
  padding: 10px 25px;
  float: right;
  font-size: 20px;
  margin: -50px 45px;
  text-decoration: none;
  color: #fff;
}

.settings-link:hover:after {
  content: '>';
  margin: 10px;
  color: #fb8859;
}

.nav {
  margin: 15px 30px;
}

.nav a {
  font-size: 20px;
  text-decoration: none;
  color: #017b76;
}

.nav a:hover:before {
  content: '<';
  margin: 10px;
  color: #fb8859;
}

.settings-wrapper {
  background: #fff;
  border-radius: 10px;
  color: black;
  display: flex;
  flex-direction: column;
}

.settings-wrapper p {
  font-weight: bold;
  color: #017b76;
  margin: 30px;
  font-size: 20px;
}

.choices-wrapper {
  display: flex;
  flex-direction: column;
  margin: 5px 30px;
}

.choices-wrapper label {
  text-transform: uppercase;
  margin: 5px;
}

.settings-wrapper button {
  border: 1px solid #fb8859;
  color: #fb8859;
  width: 90px;
  border-radius: 10px;
  margin: 5px 35px;
  font-size: 15px;
  padding: 5px;
  cursor: pointer;
  outline: none;
}

.settings-wrapper button:hover {
  background: #fb8859;
  color: #fff;
}
